import "./consiglio.js";
import { Paginazione } from "../../commons/js/render_paginazione.js";
import { StringRenderer } from "../../commons/js/string_renderer.js";

$(document).ready(function () {

    var urlParams = new URLSearchParams(window.location.search);
    console.log(urlParams.get('ref_type_id')); // "edit"
    var subMenu = urlParams.get('sub_menu_id');

    if (urlParams.has('ref_type_id')) {
        var size = 10;
        if (renderElencoType[urlParams.get("ref_type_id")] == "GRID") {
            size = 50;
        }
        var url_to_call = apibase.baseurl + urlParams.get('ref_type_id') + "?size=" + size;
        if (urlParams.has('leg')) {
            url_to_call += "&leg=" + urlParams.get('leg');
        }
        url_to_call += Paginazione.inizializzaUrl(urlParams);
        $.ajax({
            url: url_to_call,
            success: render,
        });
    } else {
        window.location = "notFound.html"
    }

    function render(result) {
        if (result != null && result.content != undefined) {

            if (renderElencoType[urlParams.get("ref_type_id")] == "GRID") {
                let tipologie = tipologieDaFiltrare[urlParams.get('ref_type_id')];
                if (tipologie != undefined && tipologie.length > 0) {
                    tipologie.forEach(function (tipo) {
                        let linkFasciaTre = result.content.filter(function (el) {
                            return el.properties.tipologia === tipo;
                        });
                        var fasciaTreHtml = "<div class=\"row \">\n";
                        if (linkFasciaTre.length > 0) {
                            if (linkFasciaTre[0].properties.legislatura != undefined) {
                                $("#title_leg").html(' Legislatura ' + linkFasciaTre[0].properties.legislatura);
                            }
                        }
                        linkFasciaTre.forEach(function (element) {
                            var iconLink = "";
                            if (element.components != null) {

                                if (element.components.immagine != undefined) {
                                    if (element.components.immagine.components.length > 0) {
                                        iconLink = baseImgUrl + element.components.immagine.components[0].properties.thumbaddress;
                                    } else {
                                        if (urlParams.get("ref_type_id") === "10010") {
                                            iconLink = "assets/consigliere_colore.svg";
                                        } else {
                                            iconLink = "assets/gruppo_consiliare_colore.svg";
                                        }
                                    }
                                } else {
                                    if (urlParams.get("ref_type_id") === "10010") {
                                        iconLink = "assets/consigliere_colore.svg";
                                    } else {
                                        iconLink = "assets/gruppo_consiliare_colore.svg";
                                    }
                                }
                            } else {
                                if (urlParams.get("ref_type_id") === "10010") {
                                    iconLink = "assets/consigliere_colore.svg";
                                } else {
                                    iconLink = "assets/gruppo_consiliare_colore.svg";
                                }
                            }
                            var gruppo = element.properties.gruppo;
                            if (gruppo == undefined) {
                                gruppo = "";
                            }

                            if (element.properties.esterno != undefined) {
                                if (element.properties.esterno === "T") {
                                    gruppo += " ESTERNO ";
                                }
                            }
                            var label_title_grid = "";
                            if (element.properties.nome != undefined) {
                                label_title_grid = element.properties.nome;
                            }
                            if (element.properties.cognome != undefined) {
                                label_title_grid += " " + element.properties.cognome;
                            }
                            if (urlParams.get("ref_type_id") === "10201") {
                                label_title_grid = element.properties.anno;
                            }
                            if (label_title_grid == "") {
                                label_title_grid = element.title;
                            }
                            var link = 'dettaglio_oggetto.html?sub_menu_id=' + subMenu + '&ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + element.id;
                            if (urlParams.has('leg')) {
                                link += "&leg=" + urlParams.get('leg');
                            }
                            if (urlParams.has("section_id")) {
                                link += "&section_id=" + urlParams.get('section_id');
                            }
                            fasciaTreHtml += ' <div class="col-6 col-sm-4  align-content-center"> <a href="' + link + '" class="dark-obj-name-bold"><div class =" text-center"><img class="img_elenco" src="' + iconLink + '" /> </div> <p class="dark-obj-name-bold mt-2 mb-0 text-center">' + label_title_grid + ' </p> </a> <p class="little-font-size mt-0 mb-5 text-center"> ' + gruppo + '  </p>  </div>';
                        });
                        fasciaTreHtml += "</div>";
                        if (linkFasciaTre.length > 0) {
                            $("#fascia_elenco").append("<h5 class='pl-4'>" + pluralStrings[tipo] + "</h5> <hr class=\"ml-4 mr-4\"/>");
                            $("#fascia_elenco").append(fasciaTreHtml);
                        }

                    });
                } else {
                    let linkFasciaTre = result.content;
                    var fasciaTreHtml = "<div class=\"row justify-content-around\">\n";
                    if (linkFasciaTre.length > 0) {
                        if (linkFasciaTre[0].properties.legislatura != undefined) {
                            $("#title_leg").html(' Legislatura ' + linkFasciaTre[0].properties.legislatura);
                        }
                    }
                    linkFasciaTre.forEach(function (element) {
                        var iconLink = "";
                        if (element.components != null) {
                            if (element.components.immagine != undefined) {
                                if (element.components.immagine.components.length > 0) {
                                    iconLink = baseImgUrl + element.components.immagine.components[0].properties.thumbaddress;

                                } else {
                                    if (urlParams.get("ref_type_id") === "10010") {
                                        iconLink = "assets/consigliere_colore.svg";
                                    } else {
                                        iconLink = "assets/gruppo_consiliare_colore.svg";
                                    }
                                }
                            }
                        } else {
                            if (urlParams.get("ref_type_id") === "10010") {
                                iconLink = "assets/consigliere_colore.svg";
                            } else {
                                iconLink = "assets/gruppo_consiliare_colore.svg";
                            }
                        }
                        var gruppo = element.properties.gruppo;
                        // var legislatura = element.properties.legislatura;

                        if (gruppo == undefined) {
                            gruppo = "";
                        }
                        var label_title_grid = element.title;
                        if (element.properties.nome != undefined) {
                            label_title_grid = element.properties.nome;
                        }
                        if (element.properties.cognome != undefined) {
                            label_title_grid += element.properties.cognome;
                        }


                        var link = 'dettaglio_oggetto.html?sub_menu_id=' + subMenu + '&ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + element.id;
                        if (detailConGrid.includes(urlParams.get('ref_type_id'))) {
                            link = 'dettaglio_griglia_oggetti.html?sub_menu_id=' + subMenu + '&ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + element.id;
                        }
                        if (urlParams.has('leg')) {
                            link += "&leg=" + urlParams.get('leg');
                        }
                        if (urlParams.get("ref_type_id") === "10201") {
                            label_title_grid = element.properties.anno;
                            link = 'dettaglio_bilancio.html?sub_menu_id=' + subMenu + '&ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + element.id;

                        }
                        if (urlParams.has("section_id")) {
                            link += "&section_id=" + urlParams.get('section_id');
                        }

                        fasciaTreHtml += ' <div class="col-4  align-content-center"> <a href="' + link + '" class="dark-obj-name-bold"><div class =" text-center"><img class="img_elenco" src="' + iconLink + '" /> </div> <p class="dark-obj-name-bold text-center mt-2 mb-0">' + label_title_grid + ' </p> </a> <p class="little-font-size mt-0 ml-4 mb-5"> ' + gruppo + '  </p>  </div>';
                    });
                    fasciaTreHtml += "</div>";
                    $("#fascia_elenco").append(fasciaTreHtml);
                }
            } else {
                if (oTypesLista.includes(urlParams.get("ref_type_id"))) {
                    var newsHTML = "<div class=\"row ml-3\">\n";
                    let elencoNews = result.content;
                    if (elencoNews.length > 0) {
                        if (elencoNews[0].properties.legislatura != undefined) {
                            $("#title_leg").html(' Legislatura ' + elencoNews[0].properties.legislatura);
                        }
                    }
                    elencoNews.forEach(function (single) {
                        var paginaDettaglio = 'dettaglio_oggetto.html?ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + single.id;
                        if (detailConGrid.includes(urlParams.get('ref_type_id'))) {
                            paginaDettaglio = 'dettaglio_griglia_oggetti.html?ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + single.id;
                        }
                        if (detailNews.includes(urlParams.get('ref_type_id'))) {
                            paginaDettaglio = 'notizia.html?ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + single.id;

                        }

                        if (urlParams.get('ref_type_id') == 10061) {
                            paginaDettaglio = 'pagina_ufficio.html?ref_type_id=' + urlParams.get('ref_type_id') + '&detail_obj_id=' + single.id;

                        }
                        if (urlParams.has('sub_menu_id')) {
                            paginaDettaglio += "&sub_menu_id=" + urlParams.get('sub_menu_id');
                        }
                        if (urlParams.has('leg')) {
                            paginaDettaglio += "&leg=" + urlParams.get('leg');
                        }
                        if (urlParams.has('section_id')) {
                            paginaDettaglio += "&section_id=" + urlParams.get('section_id');
                        }

                        var footerElemento = "";
                        var stringedDate = "";
                        if (urlParams.get('ref_type_id') != 10061) {
                            if (single.properties.data_pubblicazione != undefined) {
                                stringedDate = StringRenderer.renderDataTestuale(single.properties.data_pubblicazione);
                            } else if (single.properties.data_ora_pubblicazione != undefined) {
                                stringedDate = StringRenderer.renderDataTestuale(single.properties.data_ora_pubblicazione);
                            } else if (single.lastModifiedDate) {
                                stringedDate = StringRenderer.renderDataTestuale(single.lastModifiedDate);
                            }
                        }

                        var descrizione = "";
                        var sottotitolo = "";

                        if (single.properties.descrizione != undefined && urlParams.get('ref_type_id') != 10030) {
                            descrizione = single.properties.descrizione;
                        }
                        if (single.properties.sottotitolo != undefined) {
                            sottotitolo = single.properties.sottotitolo;
                        }
                        if (single.properties.intestazione != undefined) {
                            descrizione = single.properties.intestazione;
                        }
                        if (descrizione != "") {
                            newsHTML += ' <div class="col-12 dark-obj-name"> ' + stringedDate + '</div> <div class="col-12">  <h5 class="dark-obj-name-bold"><a class="dark-obj-name" href="' + paginaDettaglio + '">' + single.title + " " + sottotitolo + ' <svg class="icon icon-arrow-right"><use xlink:href="svg/sprite.svg#it-chevron-right"></use></svg> </a> </h5></div> <div class="col-12"> <p> ' + descrizione + '  </p> ' + footerElemento + ' <hr class="separator bordered"/> </div> ';
                        } else {
                            newsHTML += ' <div class="col-12 dark-obj-name"> ' + stringedDate + '</div> <div class="col-12">  <h5 class="dark-obj-name-bold"><a class="dark-obj-name" href="' + paginaDettaglio + '">' + single.title + " " + sottotitolo + ' <svg class="icon icon-arrow-right"><use xlink:href="svg/sprite.svg#it-chevron-right"></use></svg> </a> </h5> ' + footerElemento + ' <hr class="separator bordered"/> </div>  ';
                        }

                    });
                    $("#fascia_elenco").append(newsHTML);

                } else {
                    $("#fascia_elenco").append("");
                }
            }

            $("#fascia_elenco").append(Paginazione.renderizzaComponente(result));

            if (result.totalPages == 0) {
                if (urlParams.has('leg')) {
                    $("#title_leg").html(" Legislatura " + urlParams.get('leg'));
                }
                $("#fascia_elenco").html("Pagina in costruzione");
            }

        }
    }


});